<template>
  <div>
    <p>. {{ this.getUserInfo }} </p>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions } from 'vuex'



export default {
  data(){
    return {
      name: null,
    };
  },
  methods : {
    ...mapActions(['updateUser','initializeSections']),
    async getInfo(){
      try {
        console.log(this.accessToken)
        const response = await axios.get("/v1/nid/me", {
          headers: {
            Authorization: `Bearer ${this.accessToken}`, // Access Token Authorization  
          },
        });
        const userInfo=response.data.response.birthyear+response.data.response.name
        this.$store.dispatch('updateUser', userInfo);

        this.setCookie("name", userInfo)        //           

        window.location.href = "/";
      } catch (error) {
        console.error("   :", error);
        window.location.href = "/";
      }
    },
    setCookie(name, value, days) {
    //const date = new Date();
    //date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
    document.cookie = `${name}=${value};path=/`;
    },
  },
  mounted() {
    const naver_id_login = new window.naver_id_login(
      {clientId:"3UpoleTgLO4RWnFSsh8p",
       callbackUrl: "http://localhost:8080/login/naver"
      });
    const accessToken =naver_id_login.getAccessToken()
    console.log("12")
    console.log("access token", accessToken); //     access token 
    this.accessToken = accessToken
    this.getInfo()
    console.log("")
  }
}

</script>